.random-link {
    padding: 0 22px 0 52px;
    background-color: #8a9193;
    color: #ffffff;
    font-size: 18px;
    line-height: 36px;
    font-weight: 600;
    font-family: 'OpenSans-SemiBold';
    border-radius: 25px;
    position: relative;
    display: block;
    max-width: max-content;
    margin: 0 auto 40px;
    &:hover {
        background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(244, 67, 103, 1) 100%);
    }
    &::before {
        position: absolute;
        content: "";
        width: 24px;
        height: 24px;
        background-size: cover;
        background-repeat: no-repeat;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
        background-image: url('../../assets/images/icons/link-arrow.png');
    }
}