.header {
    padding-top: 14px;
    background-color: #ffffff;
    border-bottom: 1px solid #8a9193;

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.user {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    a {
        color: #8a9193;
        margin-left: 26px;
        transition: all .3s;
        font-weight: 600;
        font-family: 'OpenSans-SemiBold';
        &:hover{
            text-shadow: 4px 4px 2px rgba(0,0,0,0.6);
        }
    }

    &__title {
        color: #000000 !important;
        position: relative;
        padding-left: 35px;
        font-weight: 600;
        font-family: 'OpenSans-SemiBold';
        &::before {
            left: 0;
            background-image: url('../../assets/images/icons/user-icon.png');
            background-position: center;
            background-size: cover;
            width: 32px;
            height: 32px;
            position: absolute;
            content: "";
        }

        text-transform: uppercase;
        font-size: 20px;
        line-height: 136%;
    }

    &__profile {
        position: relative;
        padding-left: 35px;

        &::before {
            left: 0;
            background-image: url('../../assets/images/icons/lock.png');
            background-position: center;
            background-size: cover;
            width: 26px;
            height: 26px;
            position: absolute;
            content: "";
        }
    }

    .help {
        position: relative;
        padding-left: 35px;

        &::before {
            left: 0;
            background-image: url('../../assets/images/icons/question.png');
            background-position: center;
            background-size: cover;
            width: 26px;
            height: 26px;
            position: absolute;
            content: "";
        }
    }

    .exit {
        position: relative;
        padding-left: 35px;

        &::before {
            left: 0;
            background-image: url('../../assets/images/icons/exit.png');
            background-position: center;
            background-size: cover;
            width: 26px;
            height: 26px;
            position: absolute;
            content: "";
        }
    }
}