.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 500px;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;

    &__item+&__item {
        margin-left: 10px;
    }

    &__link {
        font-weight: 400;
        font-family: 'OpenSans-Regular';
        box-sizing: border-box;
        padding: 12px;
        border-radius: 20px;
        font-size: 18px;
        line-height: 100%;
        transition: all .3s;

        &:hover {
            color: #ffffff;
            background-color: red;
        }
    }
}