.data {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 30px;

    &__item {
        max-width: 220px;
        width: 100%;
    }

    &__item:not(:last-child) {
        padding-right: 20px;
    }

    &__item-title {
        margin-bottom: 20px;
        font-weight: 400;
        color: #8a9193;
        font-size: 18px;
        line-height: 100%;
    }
    &__item-text{
        font-weight: 400;
        color: #000000;
        font-size: 18px;
        line-height: 100%;
    }
}