.file {
    &__label {
        display: block;
        color: #8a9193;
        font-size: 17px;
        line-height: 24px;
        margin-bottom: 6px;
        font-weight: 400;
        font-family: 'OpenSans-Regular';
    }

    &__input {
        margin-bottom: 6px;
    }
}

.load-field {
    margin-bottom: 40px;
    position: relative;
    &__item {
        opacity: 0;
        overflow: hidden;
        position: absolute;
        max-width: 900px;
        width: 100%;
        font-weight: 400;
        font-family: 'OpenSans-Regular';
        height: 100px;
        z-index: 200;
    }

    &__item+&__label {
        font-size: 20px;
        line-height: 100%;
        color: white;
        box-sizing: border-box;
        width: 100%;
        display: grid;
        justify-content: center;
        align-items: center;
        height: 100px;
        background-color: #e2d9d9;
        border-bottom: 2px solid #8a9193;
        font-weight: 700;
        opacity: 0.8;

        div {
            max-width: max-content;
        }
    }
}