.entry {
    display: grid;
    justify-content: center;
    align-content: center;
    background-color: #8a9193;
    width: 100%;
    height: 100vh;
}

.popup {
    position: relative;
    border-radius: 15px;
    text-align: center;
    min-width: 800px;
    width: 100%;
    background-color: #ffffff;
    padding: 20px;
    min-height: 433px;
    &-close{
        position: absolute;
        top: 20px;
        right: 20px;
        width: 40px;
        height: 40px;
        img{
            width: 40px;
            height: 40px;
        }   
    }
    &__form {
        width: 100%;

        &-wrapper {
            display: flex;
            flex-direction: column;
            margin-bottom: 20px;
            border-bottom: 1px solid #8a9193;
        }

        &-label {
            font-size: 20px;
            line-height: 100%;
            color: #8a9193;
            margin-bottom: 10px;
        }

        &-input {
            box-sizing: border-box;
            border: none;
            font-size: 20px;
            line-height: 100%;
            color: #000000;
            padding: 14px 20px;
            transition: all .3s;
            &:focus{
                background-color: #FFE787;
            }
            &::placeholder {
                font-size: 20px;
                line-height: 100%;
                color: #000000;
            }
        }
    }

    &__title {
        font-size: 22px;
        line-height: 100%;
        text-transform: uppercase;
        color: #000000;
        margin-bottom: 50px;
    }

    .forget-password,
.enter {
    font-size: 20px;
    line-height: 100%;
    display: block;
}

.forget-password {
    max-width: max-content;
    margin: 0 auto 20px;
    color: red;
    border-bottom: 1px solid red;
}

.enter {
    text-transform: uppercase;
    height: 60px;
    line-height: 60px;
    padding: 0 30px 0 30px;
    font-size: 24px;
    font-weight: 400;
    max-width: 140px;
    margin: 0 auto;
    background-color: red;
    color: #ffffff;
    box-sizing: border-box;
    border-radius: 30px;
    transition: all .3s;
    &:hover {
        background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(244, 67, 103, 1) 100%);
    }
}

.radio{
    margin-bottom: 30px;
}

}
