.main__comments {
    background-color: #8a9193;
    max-width: 383px;
    width: 100%;
    position: relative;
}

.comment-block {
    position: fixed;
    border-radius: 5px;
    background-color: #ffffff;
    padding: 40px;
    box-sizing: border-box;
    text-align: center;
    max-width: 350px;
    margin: 0 auto 0 16px;
    width: 100%;

    &__title {
        font-size: 26px;
        line-height: 32px;
        margin-bottom: 20px;
        font-weight: 600;
        font-family: 'OpenSans-SemiBold';
    }

    &__text {
        max-width: 100%;
        padding: 8px;
        font-size: 18px;
        font-weight: 300;
        font-family: 'OpenSans-Light';
        height: 150px;
        box-sizing: border-box;
        border: none;
        border-bottom: 2px solid #8a9193;
        margin-bottom: 20px;
        resize: none;

        &::placeholder {
            font-size: 18px;
            font-weight: 400;
        }
    }

    &__btn {
        margin: 0 auto;
        color: #ffffff;
        background-color: red;
        border-radius: 20px;
        box-sizing: border-box;
        padding: 10px 15px;
        display: block;
        transition: all .3s;
        font-weight: 400;
        font-family: 'OpenSans-Regular';
        &:hover {
            background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(244, 67, 103, 1) 100%);
        }
    }
}