.save-btn {
    background-color: red;
    color: #ffffff;
    font-size: 18px;
    line-height: 36px;
    font-weight: 400;
    font-family: 'OpenSans-Regular';
    text-align: center;
    max-width: 190px;
    width: 100%;
    display: block;
    margin: 50px auto 0;
    padding: 8px 20px;
    border-radius: 25px;
    transition: all .3s;
    &:hover {
        background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(244, 67, 103, 1) 100%);
    }
}