.grid-list {}

.grid-el {
    display: grid;
    justify-content: flex-start;
    grid-column-gap: 10px;
    grid-template-columns: 30px 190px 180px 180px 180px;

    &__col {
        width: 100%;
        flex-grow: 2;
        display: flex;
        flex-direction: column;
    }

    &__col:first-child{
        max-width: max-content;
    }

    &__col:not(:first-child){
        margin-right: 30px;
    }

    &__title {
        color: #aab1b2;
        margin-bottom: 20px;
    }
    &__text{
        font-size: 17px;
        line-height: 100%;
    }
}