:root {
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;

  --blue: #007bff;
  --indigo: #6610f2;
  --purple: #6f42c1;
  --pink: #e83e8c;
  --red: #dc3545;
  --orange: #fd7e14;
  --yellow: #ffc107;
  --green: #28a745;
  --teal: #20c997;
  --cyan: #17a2b8;
  --white: #fff;
  --gray: #6c757d;
  --gray-dark: #343a40;
  --primary: #007bff;
  --secondary: #6c757d;
  --success: #28a745;
  --info: #17a2b8;
  --warning: #ffc107;
  --danger: #dc3545;
  --light: #f8f9fa;
  --dark: #343a40;

  --font-family-sans-serif: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  --font-family-monospace: SFMono-Regular,Menlo,Monaco,Consolas,"Liberation Mono","Courier New",monospace;
}

/*
    font-family: 'Roboto', sans-serif;
*/

@font-face{
  src: url('/src/fonts/roboto/Roboto-Regular.woff');
  font-weight: 400;
  font-family: 'Roboto-Regular';
  font-style: normal;
  font-display: swap;
}

@font-face{
  src: url('/src/fonts/roboto/Roboto-Medium.woff');
  font-weight: 500;
  font-family: 'Roboto-Medium';
  font-style: normal;
  font-display: swap;
}

@font-face{
  src: url('/src/fonts/markerfelt_wide.woff');
  font-weight: 700;
  font-family: 'markerfelt_wide';
  font-style: normal;
  font-display: swap;
}