a{
    text-decoration: none !important;
    transition: all .3s;
    display: inline-block;
    color: inherit;
}

html, body{
    margin: 0;
    padding: 0;
}

h1,
h2,
h3,
h4 {
    margin: 0;
}

ul {
    padding: 0;
    list-style: none;
    margin: 0;
}

.text{
    p{
        margin-bottom: 0;
    }
}

button {
    padding: 0;
    cursor: pointer;
    box-sizing: border-box;
    background-color: transparent;
    border: none;
}


