.sample {
    border-bottom: 2px dashed #8a9193;
    margin-bottom: 20px;
    padding-bottom: 20px;

    &__img-link {
        max-width: 205px;
        width: 100%;

        img {
            object-fit: cover;
            width: 100%;
            max-height: 205px;
        }
    }

    &__inner {
        display: flex;
    }

    &__content {
        flex-grow: 3;
        margin-left: 30px;

        &-title {
            font-size: 18px;
            line-height: 30px;
            margin-bottom: 5px;
            font-weight: 400;
            font-family: 'OpenSans-Regular';
        }
        &-about{
            display: flex;
        }
        .select{
            height: 34px;
            display: block;
            cursor: pointer;
        }
        .select-wrapper{
            display: block;
            margin-left: auto;
            position: relative;
            .select__custom{
                left: -80px;
                position: absolute;
                font-weight: 600;
                font-family: 'OpenSans-SemiBold';
            }
        }
    }
}