.buttons {
    &__item {
        cursor: pointer;
        padding: 15px 14px;
        box-sizing: border-box;
        color: #3d4347;
        font-size: 20px;
        font-weight: 600;
        font-family: 'OpenSans-SemiBold';
        line-height: 100%;
        text-align: center;
        position: relative;
        transition: all .3s;

        &:hover {
            text-shadow: 4px 4px 2px rgba(0, 0, 0, 0.6);
        }
    }

    &__item-counter {
        position: absolute;
        top: 4px;
        right: 0;
        font-size: 16px;
        line-height: 100%;
        color: #000000;
    }
}