.search {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 30px;

    &__wrapper {
        flex-grow: 3;
        margin-right: 50px;
        max-width: 250px;
        width: 100%;
        border-bottom: 2px solid #8a9193;
        position: relative;
        width: 44px;
        height: 44px;
    }

    &__input {
        font-size: 16px;
        line-height: 100%;
        color: #8a9193;
        box-sizing: border-box;
        padding: 5px;
        border: none;
        background-color: transparent;
        max-width: 250px;
        height: 100%;
        width: 100%;

        &::placeholder {
            font-size: 16px;
            line-height: 100%;
            color: #8a9193;
        }
    }

    &__btns {
        margin-left: auto;
    }
}