.case {
    margin-bottom: 40px;
    max-width: 900px;
    width: 100%;
    border: 1px solid #DEDFE0;
    border-radius: 5px;
    &__item {
        padding: 20px 20px 0 20px;
        border-radius: 5px;
        background-color: #ffffff;
    }

    &__info-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__title {
        font-size: 26px;
        line-height: 32px;
        font-weight: 500;
        color: #000000;
        font-weight: 600;
        font-family: 'OpenSans-SemiBold';
    }

    &__item-content{
        background-color: #f4f6f8;
        padding: 40px;
    }
}

.primary-content{
    display: flex;
    justify-content: space-between;
    &__consult{
        max-height: 40px;
        box-sizing: border-box;
        padding: 0 52px 0 22px;
        border: 2px solid #8a9193;
        color: #8a9193;
        font-size: 20px;
        line-height: 100%;
        border-radius: 20px;

    }
}

.data-primary,
.data-secondary, .gender {
    color: #8a9193;
    font-size: 18px;
    line-height: 100%;
    margin-bottom: 10px;
    font-weight: 600;
    font-family: 'OpenSans-SemiBold';
    span {
        font-weight: 400;
        font-family: 'OpenSans-Regular';
        color: #000000;
    }
}

.ending{
    &__btn{
        max-width: initial;
    }
}

.case-hidden{
    display: none;
}

.case-active{
    display: block;
}