

.load, .account-load{
    max-width: 900px;
    width: 100%;
    padding: 24px;
    background-color: #eaeaea;
    border-radius: 5px;
    position: relative;
    &__title{
        font-size: 20px;
        line-height: 100%;
        text-transform: uppercase;
        text-align: center;
        margin-bottom: 60px;
        font-weight: 600;
        font-family: 'OpenSans-SemiBold';
    }
    &-close{
        position: absolute;
        right: 14px;
        top: 14px;
    }
}

.load-inactive{
    display: none;
}

.load-active{
    display: block;
    z-index: 200;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
}

.body-active{
    height: 100vh;
    z-index: 100;
    position: relative;
    display: grid;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
}