.main {
    &__inner {
        display: flex;
        justify-content: flex-start;
    }

    &__side {
        max-width: 160px;
        width: 100%;
        background-color: #3d4347;
    }
}

.content {
    max-width: 1020px;
    flex-grow: 3;
    padding: 40px;
    background-color: #f4f6f8;

    &__item {
        background-color: #ffffff;
        padding: 36px;
        border-radius: 8px;
        -webkit-box-shadow: 7px 8px 8px 0px rgba(34, 60, 80, 0.2);
        -moz-box-shadow: 7px 8px 8px 0px rgba(34, 60, 80, 0.2);
        box-shadow: 7px 8px 8px 0px rgba(34, 60, 80, 0.2);
    }

    &__list{
        display: block;
        position: relative;
        padding-bottom: 80px;
    }

    &__item-info {
        display: flex;
    }

    &__item-description {
        flex-grow: 3;
        margin-left: 350px;
    }

    &__item+&__item {
        margin-top: 30px;
    }

    &__item-title {
        font-size: 26px;
        line-height: 32px;
        margin-bottom: 20px;
        color: #333333;
        font-weight: 600;
        font-family: 'OpenSans-SemiBold';
    }

    &__item-link {
        font-size: 18px;
        line-height: 36px;
        background-color: #8a9193;
        font-weight: 600;
        font-family: 'OpenSans-SemiBold';
        color: #ffffff;
        border-radius: 20px;
        height: 40px;
        text-align: center;
        padding: 0 22px;
        margin-top: 20px;
        transition: all .3s;
        &:hover {
            background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(244, 67, 103, 1) 100%);
        }
    }

    &__item-subtitle {
        font-size: 16px;
        line-height: 28px;
        color: #8a9193;
        font-weight: 600;
        font-family: 'OpenSans-SemiBold';

    }

    &__item-textarea {
        box-sizing: border-box;
        padding: 10px 20px;
        color: #000000;
        max-width: 500px;
        min-height: 150px;
        width: 100%;
        font-size: 18px;
        line-height: 100%;
        line-height: 28px;
        transition: all .3s;
    }
}

.specialization {
    color: #8a9193;
    font-weight: 600;
    font-family: 'OpenSans-SemiBold';
}

.data {
    font-weight: 400;
    font-family: 'OpenSans-Regular';
    color: #000000;
}

.data {
    span {
        font-weight: 600;
        font-family: 'OpenSans-SemiBold';
        color: #8a9193;
    }
}