.add-smth {
    background-color: #8a9193;
    color: #ffffff;
    font-size: 18px;
    line-height: 36px;
    border-radius: 25px;
    padding: 4px 24px 4px 44px;
    position: relative;
    max-width: 230px;
    display: block;
    margin: 0 auto;
    max-height: 44px;

    &:hover {
        background: radial-gradient(circle, rgba(63, 94, 251, 1) 0%, rgba(244, 67, 103, 1) 100%);
    }

    &::before {
        position: absolute;
        content: "";
        width: 24px;
        height: 24px;
        left: 15px;
        top: 50%;
        transform: translateY(-50%);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url('../../assets/images/icons/add-smth-1.png');
    }
}